#loading-message {
  display: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  background-color: #f2f2f2;
  padding: 20px;
}

h1 {
  margin: 0;
}

.main {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.search-form {
  margin-bottom: 20px;
}

.gallery {
  grid-gap: 10px;
  grid-template-columns: repeat(4, 1fr);
  display: grid;
}

.photo-card {
  border: 1px solid #ccc;
  padding: 10px;
}

.photo-card img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  margin: 20px auto;
  padding: 10px 20px;
  display: block;
}

.load-more:hover {
  background-color: #45a049;
}

